// 
$mobile-width-only: 767px;
$tablet-width: 768px;
$desktop-width: 1200px;

// 
$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

//
$ptsans: "PT Sans", "Arial", sans-serif;

// 
$white: hsl(0, 0%, 100%);
$blue: hsl(203, 47%, 66%);
$blue--hover: hsl(203, 42%, 58%);
$blue--active: hsl(202, 44%, 54%);
$gray: hsl(0, 0%, 95%);
$gray-light: hsl(0, 0%, 90%);
$black: hsl(0, 0%, 0%);

// 
$bg-basic: $white;
$bg-main: $blue;
$bg-secondary: $gray;
$bg-features: hsl(0, 0%, 93%);
$bg-map: hsl(40, 20%, 94%);

// 
$border-dark: hsl(203, 27%, 53%);
$border-light: hsl(203, 47%, 73%);

// 
$text-main: hsl(0, 0%, 20%);
$text-light: $white;
$text-dark: $black;
$text-opacity: hsla(0, 0%, 100%, 0.3);

// 

$link-main: $white;
$link-dark: $black;
$link-dark--hover: hsl(203, 47%, 66%);
$link-dark--active: hsla(0, 0%, 0%, 0.3);
$active-link-dark: hsl(23, 15%, 40%);
$active-link-dark--hover: hsl(23, 18%, 32%);
$active-link-dark--active: hsla(23, 15%, 40%, 0.3);

//     

$bg-link-main: $blue;
$bg-link-main--hover: $blue--hover;
$bg-link-main--active: $blue--active;

//   

$btn-main: $blue;
$btn--hover: $blue--hover;
$btn--active: $blue--active;
$btn--disabled: $gray-light;

//   
$bg-input: $gray;
$bg-input--hover: $gray-light;
$bg-input--active: $white;
$border-input--active: $gray-light;
$bg-icon: $gray-light;
$bg-icon--checked: $black;
